.page-template-page-zimmer
    .footer-main
        border-top: 0

@keyframes sliding-left
    0%
        transform: translateX( 0% )
    100%
        transform: translateX( -100% )

.section-zimmer
    &__zimmer, &__group-zimmer
        .zimmer-link
            @apply relative block overflow-hidden lg:h-[400px] lg:max-h-[400px] xl:h-[689px] xl:max-h-[689px] border-x border-solid
            z-index: 2
            &--image
                @apply h-auto lg:h-full
                z-index: 1
                img
                    @apply h-auto lg:h-full
            &--title-wrapper
                @apply relative flex justify-between items-stretch h-[65px] pl-5 border-b border-solid border-black transition-all duration-500 ease-in-out bg-white overflow-hidden
                z-index: 4
                h2
                    @apply self-center m-0 transition-all duration-300 ease-in-out opacity-100 translate-y-0 whitespace-nowrap text-ellipsis overflow-hidden
            &--icon
                @apply relative w-[65px] h-full flex justify-center items-center border-l border-solid border-black bg-bg-color transition-all duration-300 ease-in-out bg-white
                svg
                    path
                    fill: #fff
            &--overlay
                @apply lg:absolute top-0 left-0 w-full h-full lg:transition-all lg:duration-500 lg:ease-in-out lg:translate-y-full bg-bg-color flex flex-col lg:justify-center px-8 py-8 xl:py-0 xl:pl-32 xl:pr-40
                z-index: 3
            &:hover
                .zimmer-link--title-wrapper
                    h2
                        @apply translate-x-full opacity-0
                .zimmer-link--overlay
                    @apply translate-y-0
                .zimmer-link--title-overlay
                    @apply translate-x-0 opacity-100
    &__group-zimmer
        .zimmer-link
            @apply h-[500px] max-h-[495px]
            &--overlay
                @apply lg:pl-52
                p
                    @apply max-w-[660px]

.event
    &__image
        img
            border: 1px solid

.section-zimmer
    &__intro
        .wp-block-columns
            @apply gap-8
            .wp-block-column
                @apply text-black text-sm font-sans font-normal leading-8 mb-8 last-of-type:mb-0

.gastro
    &__images
        img
            @apply w-full h-full

//Responsive
@media ( max-width: 1024px )
    .hotel
        &__header-img
            background-position:-450px 0

@media ( max-width: 640px )
    .hotel
        &__header-img
            background-position:-375px 0

@media ( max-width: 400px )
    .hotel
        &__header-img
            background-position:-345px 0