.section-blog
  .card-blog
  
.blog-grid-pagination
  @apply flex mt-10
  li
    @apply block py-2 px-4 mr-4 border transition-all duration-300 ease-in-out font-sans font-normal
    background-color: transparent
    &.selected
      @apply bg-white
    &:hover
      @apply cursor-pointer bg-white